import datetime from '@/utils/datetime.js'
import url from "@/request/url.js"
import {
	mapState
} from 'vuex'
export default {
	data() {
		return {
			date: datetime.getCurrentDate(),
			event: ''
		}
	},
	computed: {
		...mapState({
			userInfo: state => state.userInfo
		})
	},
	methods: {
		addCountDown() {
			if (this.event == '') {
				uni.showToast({
					title: "别忘了输入内容哦！",
					icon: "none"
				});
				return;
			}
			uni.request({
				url: url.addCountDown,
				method: "POST",
				data: {
					uid: this.userInfo.id,
					event: this.event,
					endTime: this.date
				},
				success: resp => {
					uni.showToast({
						title: "添加成功！",
						icon: "success"
					});
					uni.navigateBack({
						delta: 1,
						animationDuration: 100,
						animationType: "slide-out-right"
					});
				}
			})
		},
		dateChange(e) {
			this.date = e.detail.value
		}
	},
}
